.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.75);
  height: 100vh;
  width: 100vw;
}

.container {
  position: relative;
}

.storyx {
  cursor: pointer;
  color: white;
  position: absolute;
  top: 5px;
  right: 15px;
  z-index: 100;
  font-family: "Nunito", sans-serif;
  font-size: 2rem;
}

.storypic {
  height: 80vh;
  width: 50vw;
  border-radius: 16px;
  object-fit: cover;
}

.storycontainer {
  position: absolute;
  bottom: 30px;
  left: 15px;
  color: white;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 16px;
  padding: 10px;
  font-size: 1.15rem;
  line-height: 2;
  overflow-wrap: break-word;
  word-break: break-word;
  max-width: 40vw;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
}

.comment {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 18vw;
  display: block;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  box-shadow: none;
  grid-row: 2/3;
  grid-column: 2/3;
}

.username {
  font-weight: bold;
  font-size: 1rem;
  color: #e8f5fd;
  grid-row: 1/2;
  grid-column: 2/3;
}

.avatar {
  height: 5vh;
  width: 5vw;
  grid-row: 1 / 3;
  grid-column: 1 / 2;
  border-radius: 999px;
  margin-right: 10px;
}

.heartcontainer {
  position: absolute;
  bottom: 30px;
  right: 15px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
  height: 75px;
  width: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

.notliked {
  color: white;
  font-size: 3rem;
  transform: scale(1);
  transition: color 0.2s, transform 0.2s;
}

.liked {
  color: pink;
  font-size: 3rem;
  transform: scale(1.2);
  transition: color 0.2s, transform 0.2s;
}

@media (max-width: 600px) {
  .storypic {
    height: 100vh;
    width: 100vw;
    border-radius: 0;
  }

  .container {
    height: 100vh;
    width: 100vw;
  }

  .storycontainer {
    max-width: 65vw;
  }

  .comment {
    max-width: 58vw;
  }
}

@media (orientation: portrait) and (max-width: 600px) {
  .avatar {
    width: 10vw;
  }
}

@media (orientation: landscape) and (max-width: 600px) {
  .avatar {
    height: 10vh;
  }
}
