* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  max-width: 100vw;
  height: 100%;
  background: #f5f8fa;
}

html {
  overflow-y: scroll;
  scrollbar-width: none;
}

body.modal-open {
  overflow: hidden !important;
  height: 100%;
  position: fixed;
  width: 100vw;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.images {
  height: 400px;
  width: 400px;
  max-width: 100%;
  object-fit: cover;
  border-radius: 12px;
  margin: 5px;
}

.noImageText {
  font-family: "Nunito", sans-serif;
  font-size: 2rem;
  padding: 20px;
}

.subtitle {
  font-family: "Nunito", sans-serif;
  font-weight: bold;
  padding: 5px;
  font-size: 2.5rem;
}

@media (max-width: 900px) {
  .images {
    width: 170px;
    height: 170px;
  }
}
