.stories {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    overflow-x: auto;
    scrollbar-width: none;
    margin: 5px;
    gap: 15px;
    flex-wrap: nowrap;
}

.stories::-webkit-scrollbar {
    display: none;
}

.profilePic {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    outline: none;
    box-shadow: none;
    flex-shrink: 0;
}

img {
    outline: none;
}

img:active, img:focus {
    outline: none;
    box-shadow: none;
}

@media (max-width: 600px) {
    .profilePic {
        height: 100px;
        width: 100px;
    }
}