* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 8vh;
  min-height: 100px;
  background: #e8f5fd;
  border-bottom: 2px solid #e1e8ed;
}

.logo {
  width: 10vw;
  min-width: 200px;
  height: 6rem;
  border-radius: 12px;
}

.smartphonelogo {
  display: none;
}

.buttonDiv {
  display: flex;
  margin: 10px;
  gap: 10px;
  align-items: center;
}

.searchBar {
  font-size: 20px;
  font-family: "Nunito", sans-serif;
  border-radius: 999px;
  width: 40vw;
  max-width: 600px;
  height: 3.5rem;
  border: none;
  border: 1px solid #1da1f2;
  padding: 25px;
}

.searchBar:focus {
  border: 2px solid orange;
  transition: border-color 0.2s;
  outline: none;
}

.button {
  border-radius: 999px;
  font-size: 1.5rem;
  font-weight: bold;
  font-family: "Nunito", sans-serif;
  width: 10vw;
  min-width: 100px;
  max-width: 150px;
  height: 3.5rem;
  background: linear-gradient(90deg, #1da1f2 0%, #0a85d9 100%);
  color: #fff;
  border: none;
  cursor: pointer;
}

@media (max-width: 600px) {
  .button {
    min-width: 60px;
    border-radius: 50%;
  }
  .searchBar {
    width: 50vw;
  }
  .logo {
    display: none;
  }
  .smartphonelogo {
    display: block;
    width: 75px;
    height: 75px;
    border-radius: 12px;
    margin: 5px;
  }
}
