.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.75);
  overflow: hidden;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  background-color: #f5f8fa;
  width: 90vw;
  max-width: 1200px;
  height: 60vh;
}

.buttonArea {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 90vw;
  max-width: 1200px;
}

.button {
  font-size: 1.15rem;
  font-weight: bold;
  color: white;
  font-family: "Nunito", sans-serif;
  height: 50px;
  width: 20vw;
  min-width: 150px;
  max-width: 300px;
  border-radius: 999px;
  border: none;
  margin: 10px;
  background-color: #1da1f2;
  cursor: pointer;
}

.dropzone {
  border: 2px #aaa dotted;
  height: 40vh;
  width: 85vw;
  max-width: 1100px;
  margin: 10px;
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 12px;
}

.dropzoneActive {
  background-color: lightblue;
}

.hidden {
  display: none;
}

.link {
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  border: none;
  background-color: #f5f8fa;
  font-size: 1.25rem;
  font-family: "Nunito", sans-serif;
}

.p {
  font-size: 1.25rem;
  font-family: "Nunito", sans-serif;
  text-align: center;
  margin: 10px;
}

.namefield {
  width: 85vw;
  max-width: 1100px;
  height: 8vh;
  margin: 10px 0px;
  border: 1px solid #1da1f2;
  border-radius: 16px;
  text-align: center;
  line-height: 1.5;
  overflow: auto;
  scrollbar-width: none;
  font-size: 1rem;
  resize: none;
  padding-right: 10px;
}

.namefield:focus {
  border: 2px solid orange;
  transition: border-color 0.2s;
  outline: none;
}

.namefield::-webkit-scrollbar {
  display: none;
}

.namefield::placeholder {
  line-height: 8vh;
  font-size: 1rem;
  padding-left: 10px;
}
