* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.routecontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  inset: 0;
  width: 100%;
  overflow-y: auto;
  background-color: #f5f8fa;
  height: 100vh;
}

.returnBar {
  background-color: #e8f5fd;
  width: 100vw;
  height: 8vh;
  min-height: 100px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #e1e8ed;
  overflow: auto;
}

.returnBar button {
  border-radius: 999px;
  font-size: 2rem;
  font-weight: bold;
  min-width: 4rem;
  height: 4rem;
  margin: 10px;
  font-weight: bold;
  color: white;
  font-family: "Nunito", sans-serif;
  background-color: #1da1f2;
  border: none;
  cursor: pointer;
}

.returnBar button:hover {
  background-color: orange;
  transition: background-color 0.2s;
}

.returnBar p {
  font-size: 2rem;
  padding-left: 10px;
  font-family: "Nunito", sans-serif;
}

.image {
  max-height: 50vh;
  width: auto;
  aspect-ratio: 16/9;
  align-items: center;
}

.comment {
  background-color: #4da6d1;
  max-height: 50vh;
  width: 85vw;
  margin-top: 15px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  border-bottom: 2px solid #e1e8ed;
  font-family: "Nunito", sans-serif;
  font-size: 1.25rem;
  padding: 10px;
  color: white;
  line-height: 2;
  overflow-wrap: break-word;
  word-break: break-word;
  display: grid;
  grid-template-columns: auto 1fr;
}

.uploadavatar {
  height: 3em;
  width: 3em;
  border-radius: 999px;
  margin-right: 10px;
}

.imageUser {
  align-self: center;
}

.imageComment {
  grid-column: 1/3;
}

.replySection {
  background-color: #4da6d1;
  max-height: 50vh;
  width: 85vw;
  border-top: 2px solid #e1e8ed;
  overflow-wrap: break-word;
  word-break: break-word;
  display: inline-block;
  overflow-y: auto;
}

.reply {
  font-family: "Nunito", sans-serif;
  font-size: 1.25rem;
  color: white;
  line-height: 1.4;
  border-bottom: 2px solid #e1e8ed;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  align-items: start;
  padding: 10px;
}

.avatar {
  height: 3em;
  width: 3em;
  border-radius: 999px;
  grid-row: 1/3;
  grid-column: 1/2;
  margin-right: 10px;
  align-self: center;
}

.replyUser {
  grid-row: 1/2;
  grid-column: 2/3;
}

.replyComment {
  grid-row: 2/3;
  grid-column: 2/3;
  word-break: break-word;
}

.replyUser,
.replyComment {
  margin: 0;
  padding: 0;
}

.inputSection {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nunito", sans-serif;
  background-color: #e8f5fd;
  width: 85vw;
  max-height: 500px;
}

.buttons {
  font-size: 1.15rem;
  font-weight: bold;
  color: white;
  font-family: "Nunito", sans-serif;
  height: 50px;
  width: 10vw;
  min-width: 100px;
  max-width: 300px;
  border-radius: 999px;
  border: none;
  margin: 10px;
  background-color: #1da1f2;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.namefield {
  width: 50vw;
  height: 5vh;
  margin: 10px 0px;
  border: 1px solid #1da1f2;
  border-radius: 16px;
  text-align: center;
  line-height: 1.5;
  overflow: auto;
  scrollbar-width: none;
  font-size: 1rem;
  resize: none;
  padding-right: 10px;
}

.namefield:focus {
  border: 2px solid orange;
  transition: border-color 0.2s;
  outline: none;
}
